<template>
    <div class="users">
        <div class="header">
            <span @touchstart="back" class="back">
                <img src="../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">{{ info.fNickName }}的个人主页</span>
            <span class="back"></span>
        </div>
        <div class="userProfile">
            <div class="headPic">
                <img :src="info.fPhoto">
            </div>
            <div class="infor">
                <span class="inforStuff">
                    <span class="dongtai">动态</span>
                    <span @click="showMoments">{{ info.momentCount }}</span>
                </span>
                <span class="inforStuff">
                    <span class="fensi">粉丝</span>
                    <span @click="showFans">{{ info.fansCount }}</span>
                </span>
                <span class="inforStuff">
                    <span class="guanzhu">关注</span>
                    <span @click="showConcern">{{ info.interestCount }}</span>
                </span>
            </div>
        </div>
        <ul class="lessons">
            <li class="lesson" v-for="(lesson, index) in lessons" :key="index" @click="showDetails(index)">
                <img class="lessonIcon" :src="lesson.icon">
                <span class="lessonInfor">{{ lesson.infor }}</span>
            </li>
        </ul>
        <div class="footer">
            <div class="concerned">已关注</div>
            <div class="chat" @click="$router.push('/home/community/chat')">私信</div>
        </div>
    </div>
</template>

<script>
import { getUsersInfo } from '../api/getdata'
import store from '../vuex/store'
export default {
  name: 'Users',
  data () {
    return {
      info: {},
      lessons: [{
        icon: require('../assets/img/lessons.png'),
        infor: 'TA的课程',
        linkTo: 'myLessons'
      }, {
        icon: require('../assets/img/collections.png'),
        infor: 'TA的收藏',
        linkTo: 'myCollection'
      }, {
        icon: require('../assets/img/communities.png'),
        infor: 'TA的哼哈团',
        linkTo: 'myRegiment'
      }]
    }
  },
  mounted () {
    this.getPublisherInfo()
    console.log(this.info)
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    showMoments () {
      // console.log('查询动态列表')
      this.$router.push('/home/community/dynamic')
    },
    showConcern () {
      // console.log('查询关注列表')
      this.$router.push('/home/community/concerned')
    },
    showFans () {
      // console.log('查询粉丝列表')
      this.$router.push('/home/community/fans')
    },
    showDetails (index) {
      store.commit('getUserClick', 'other')
      this.$router.push(`/home/mine/${this.lessons[index].linkTo}`)
    },
    getPublisherInfo () {
      console.log(store.state.userType)
      var userType = ''
      if (store.state.userType === 'M' || store.state.userType === '0') {
        userType = '0'
      } else if (store.state.userType === 'T' || store.state.userType === '1') {
        userType = '1'
      } else if (store.state.userType === 'C' || store.state.userType === '2') {
        userType = '2'
      } else {
        userType = '3'
      }
      let data = getUsersInfo({fUserId: store.state.userId, fBelongType: userType})
      data.then(res => {
        console.log(res.data)
        this.info = res.data
      })
    }
  }
}
</script>

<style scoped>
  * {
    font-size: 0.5rem;
    margin: 0;
    padding: 0;
  }
  a {
    font-size: 0.4rem;
  }

  .users {
    width: 100%;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .headPic {
    width: 1.8rem;
    height: 1.8rem;
    border-radius: 50%;
    overflow: hidden;
    margin: 0.5rem auto;
  }

  .headPic img {
    width: 100%;
    height: 100%;
  }

  .inforStuff {
    margin: 0 0.45rem;
  }

  .inforStuff span {
    font-size: 0.4rem;
  }

  .userProfile {
    padding-bottom: 0.6rem;
    border-bottom: 3px solid #f5f5f5;
  }

  .lessons {
    list-style: none;
  }

  .lesson {
    border-bottom: 1px solid #f5f5f5;
    padding: 0.4rem 0;
    text-align: start;
    margin: 0 0.4rem;
    display: flex;
    align-items: center;
  }

  .lessonInfor {
    color: #666666;
    margin-left: 0.4rem;
  }

  .footer {
    height: 1.2rem;
    position: fixed;
    bottom: 0;
    display: flex;
    width: 100%;
  }

  .concerned,
  .chat {
    height: 1.2rem;
    line-height: 1.2rem;
    width: 50%;
    border-top: 1px solid #f5f5f5;
  }

  .concerned {
    border-right: 1px solid #f5f5f5;
  }

  .lessonIcon {
    width: 0.54rem;
    height: 0.51rem;
  }
</style>



